<template>
  <div>
<!--    <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">-->
<!--      <el-radio-button :label="false"></el-radio-button>-->
<!--      <el-radio-button :label="true"></el-radio-button>-->
<!--    </el-radio-group>-->
    <i class="el-icon-setting setting-col" @click="revert"></i>
    <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title"></span>
        </template>
        <el-menu-item-group>
          <span slot="title"></span>
          <el-menu-item index="1-1">1</el-menu-item>
          <el-menu-item index="1-2">2</el-menu-item>
        </el-menu-item-group>
        <el-menu-item-group title="2">
          <el-menu-item index="1-3">3</el-menu-item>
        </el-menu-item-group>
        <el-submenu index="1-4">
          <span slot="title">4</span>
          <el-menu-item index="1-4-1">1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title"></span>
      </el-menu-item>
      <el-menu-item index="3" disabled>
        <i class="el-icon-document"></i>
        <span slot="title"></span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-view"></i>
        <span slot="title"></span>
      </el-menu-item>
    </el-menu>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        isCollapse: true
      };
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      revert() {
        this.isCollapse = !this.isCollapse;
      }
    }
  }
</script>

<style scoped>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .setting-col{


    position: absolute;
    left: 32px;
    top: 31px;

  }

</style>



