<template>
  <div>
    <header>
      <el-steps :active="active" finish-status="success">
        <el-step title=""></el-step>
        <el-step title="spu"></el-step>
        <el-step title="sku"></el-step>
        <el-step title=""></el-step>
      </el-steps>
    </header>
    <section>
      <div>
        <el-form>
          <el-row class="category">
            <el-col :span="12">
              <el-input v-model="validateBo.brand" placeholder=":"></el-input>
            </el-col>
          </el-row>
          <!--           -->
          <el-row class="category">
            <el-col :span="12">
              <el-input v-model="validateBo.category" placeholder=": "></el-input>
            </el-col>
          </el-row>
          <!--          -->
          <el-row class="category">
            <el-col :span="12">
              <el-input v-model="validateBo.name" placeholder=": 9"></el-input>
            </el-col>
          </el-row>
          <el-row>
            <!--  -->
            <el-col :span="6">
              <el-input v-model="inputParam.k" placeholder=""></el-input>
            </el-col>
            <el-col :span="2">
              <div>&nbsp;&nbsp;&nbsp;&nbsp;</div>
            </el-col>

            <!--            -->

            <el-col :span="6">
              <el-input v-model="inputParam.v" placeholder="32G" :disabled="disableV">

              </el-input>
            </el-col>
            <el-col :span="2">
              <div>&nbsp;&nbsp;&nbsp;</div>
            </el-col>

            <el-col :span="5">
              <el-button type="success" @click="addKV"></el-button>
            </el-col>
          </el-row>
          <el-row class="category">
            <el-col :span="5">
              <!--          -->
              <el-input label="9" v-model="inputParam.subtitle"   placeholder=": 9"/>

            </el-col>
          </el-row>


          <el-row class="category">
            <el-col :span="5">
<!--          -->
              <el-input label="" v-model="inputParam.surplusCount" placeholder=""/>

            </el-col>
          </el-row>
          <el-row class="category">
            <el-col :span="5">
              <!--          -->
              <el-input label="" v-model="inputParam.price" placeholder=""/>

            </el-col>
          </el-row>


        </el-form>
      </div>


    </section>
    <section class="table">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="param"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="value"
          label=""
          width="180">
        </el-table-column>

        <el-table-column>
          <template slot-scope="scope">
            <el-button type="danger"  size="mini" icon="el-icon-delete" @click="remove(scope.$index)"></el-button>
          </template>
        </el-table-column>

      </el-table>
    </section>
    <footer>
      <el-row>
        <el-button type="success" @click="commit"></el-button>
      </el-row>
    </footer>
  </div>
</template>

<script>
  import axios from 'axios'
  import {StringUtils} from "../../../../common/utils/StringUtils";

  export default {
    name: "FormContainer",
    data() {
      return {
        active: 0,

        tableData: [],
        inputParam: {
          k: '',
          v: '',
          price:'',
          surplusCount: '',
          subtitle:'',
          param:{}

        },
        validateBo: {
          brand: '',
          category: '',
          name: '',

        },

      }
    },
    methods: {
      remove(index){
        console.log(index)
        this.tableData.splice(index,1)
      },
      validate() {

      },
      commit() {
        let spuProductName = this.validateBo.name
        let brand =          this.validateBo.brand
        let category =       this.validateBo.category
        if(StringUtils.isAnyBlank(brand,category,spuProductName)) {
          return this.$message.error("")
        }

        this.$log.print('param..',this.commitPacket())
        // 
        axios.post(`/spu/spu?brand=${brand}&category=${category}&spuProductName=${spuProductName}`)
        .then(res=>{
            console.log(res)
            if(500==res.data.code) {
              this.$message.error("")
            }
            return res.data

        }).then(data=>{
          this.$log.print('data:0',data)
          // sku
          let sku = this.commitPacket()
          sku.spuId = data.data.id
          this.$log.print('log->>',sku)
          axios.post('/sku_info',sku)
          .then(skuResult=>{
            this.$log.print('skuResult ->',skuResult)
          })
        })
      },


      addKV() {
        if (this.inputParam.v == '') return
        this.tableData.push({
          'param': this.inputParam.k,
          'value': this.inputParam.v
        })
      }
      ,
      handleCheckChange() {

      },
      commitPacket(){

          //param
          let paramList = this.tableData
          let p1        = this.inputParam
          console.log(paramList)
          for(let i =0;i<paramList.length;++i) {
            let kv = paramList[i]
            console.log(kv.param)
            console.log(kv.value)
            p1.param[kv.param] = kv.value
          }
          p1.title = p1.subtitle

          return p1

          // title
          // let title = this.


        }
    },
    computed: {
      disableV: {
        get() {
          return this.inputParam.k.replace(/^\s+|\s+$/gm, '').length <= 0
        },


      },


    }
  }
</script>

<style scoped>
  header {
    width: 500px;
    margin-bottom: 20px;
  }

  .category {
    margin-bottom: 12px;
    margin-top: 12px;
  }

</style>



