<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"

    >
<!--   start edit it    -->
      <el-form    ref="ruleForm">
        <el-form-item label=""   prop="name">
          <el-input v-model="tableForm.categoryName"  autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-select v-model="tableForm.categoryIsDeleted" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

      </el-form>

<!--      end !!!-->
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "PopUp",

    data() {
      return {
        dialogVisible:false,
        options: [{
          value: '1',
          label: ''
        },
          {
            value: '0',
            label: ''
          }
        ],

        tableForm:{
          categoryName:'',
          categoryIsDeleted:''
        }

      }





    },
    methods: {
      handleClose(done) {
        done()
        // this.$confirm('')
        //   .then(_ => {
        //     done();
        //   })
        //   .catch(_ => {
        //   });
      },
      openOrClose() {

        this.dialogVisible = true
      }
    }

  }

</script>

<style scoped>

</style>



