<template>
  <div>
    <el-dialog title=""
               @close="closePopUp"

               :visible.sync="dialogTableVisible">
      <el-table :data="gridData"

      >
        <!--      <el-table :data="gridData">-->
        <el-table-column v-for="(field,i) in fields"
                         :property="field.property"
                         :label="field.label"


                         :key="i"

        >

        </el-table-column>
        <!--        <el-table-column property="name" label="" width="200"></el-table-column>-->
        <!--        <el-table-column property="address" label=""></el-table-column>-->
      </el-table>
    </el-dialog>
  </div>

</template>

<script>

  export default {

    name: "PopUp",
    data() {
      return {
        dialogTableVisible: false,
        gridData: [],
        loading: true,
        fields: [
          {
            'property': 'title',
            'label': ''
          },
          {
            'property': 'price',
            'label': ''
          }, {
            'property': 'param',
            'label': ''
          },
          {
            'property': 'saleable',
            'label': ''
          }, {
            'property': 'lastUpdateTime',
            'label': ''
          }, {
            'property': 'surplusCount',
            'label': ''

          }
        ]
      }

    },
    methods: {
      closePopUp() {
        this.$emit('close')
      },
      openDialog(spuId) {
        this.$log.print('spuId ->', spuId)
        this.loading = true
        this.renderGrid(spuId)
      },
      async renderGrid(spuId) {
        this.dialogTableVisible = true

        let {data: result} = await this.$http.get(`/sku?spuId=${spuId}`);
        console.log("xxx--->", result)

        if (result == null || result.length == 0) {
          this.gridData = []

          this.loading = false
          return
        }
        this.$log.print(result[0].param)
        // json.param = json.param.toString()
        //result[0].param = JSON.stringify(result[0].param)
        for (let i = 0; i < result.length; ++i) {
          result[i].param = JSON.stringify(result[i].param)
          result[i].lastUpdateTime = this.$log.formatDate(result[i].lastUpdateTime)
        }
        this.gridData = result
        this.loading = true
      }
    },
    computed: {}
  }
</script>

<style scoped>

</style>



