<template>
  <div class="login_container">
    <div class="login_box">
      <div class="avater_box">
        <img src="@/assets/logo.png" alt="">
      </div>
      <!--       -->
      <el-form label-width="0px" class="login_form"
               comment=":model "
               :model="loginInfo"
               :rules="inputRules"
               ref="loginFormRef"
      >
        <el-form-item comment="" prop="username">
          <!--           -->
          <el-input prefix-icon="el-icon-user" v-model="loginInfo.username"></el-input>
        </el-form-item>
        <el-form-item comment="" prop="password">
          <el-input prefix-icon="el-icon-warning-outline" v-model="loginInfo.password" type="password"></el-input>
        </el-form-item>

        <el-form-item class="btn-line">
          <el-button type="success" @click="doPost"></el-button>
          <el-button type="info" @click="resetFields"></el-button>

        </el-form-item>


      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: "LoginPage",
    data() {
      return {
        //
        loginInfo: {
          username: '',
          password: ''
        },
        //  
        inputRules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 20, message: "120", trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 20, message: "13", trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      doPost() {
        this.$refs.loginFormRef.validate(async valid => {
          // console.log(123)
          console.log(valid);
          let result = false;
          if (!valid) return;
          try {
            let postData = {
              name: this.loginInfo.username,
              password: this.loginInfo.password
            }
            const {data: result} = await this.$http.post("/login", postData);
            console.log(result)
            if(result.code==200) {
              this.$message.success("")
              window.sessionStorage.setItem('token',result.data)
              await this.$router.push('/nav2')
            }else{
              this.$message.error("")
            }
          } catch (e) {
            console.log('')
            // this.$message.error("")
            this.$message.error("")
            // let token = '123'
            // window.sessionStorage.setItem('token',token)
            // result = true;
          }

          // result.data 
        })
      },
      resetFields() {
        this.$refs.loginFormRef.resetFields();
      }
    }
  }
</script>

<style scoped lang="less">
  .login_container {
    background-color: #111821;
    height: 100%;
    width: 100%;


    .login_box {
      width: 450px;
      height: 300px;
      background-color: white;
      border-radius: 3px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

      .avater_box {
        height: 130px;
        width: 133px;
        border: 1px solid #eeeeee;
        border-radius: 50%; /*              */
        box-shadow: 0 0 10px #ddd; /*  */
        left: 50%;
        right: 50%;
        position: absolute;
        transform: translate(-50%, -50%); /* */
        background-color: #fff;

        img {
          width: 100%;
          height: 100%;
          border-radius: 50%; /*          */
          background-color: #eeeeee;
        }

      }

      .btn-line {
        display: flex;
        justify-content: flex-end;
      }

      .login_form {
        position: absolute;
        bottom: 0;
        width: 100%;
        padding: 0 10px;
        box-sizing: border-box; /* form border-sizing  */
      }
    }

  }


</style>



