<template>
  <div class="header-block">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/shop'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search">
      <!-- form      -->
      <el-form :inline="true" :model="query" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="query.brand" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="query.letter" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>


  </div>
</template>

<script>
  export default {
    name: "SearchInputHeader",
    data() {
      return {
        query:{
          letter:'',
          brand:''
        }
      }
    },
    methods:{
      onSubmit() {
        this.$emit('_search',this.query)

      }
    }
  }
</script>

<style scoped>
  .search{
    margin-top: 13px;
  }

</style>



