<template>
  <div>
    <el-card>
      <!--      <el-row>-->
      <!--        <el-col>-->
      <!--          <el-button type="primary"></el-button>-->
      <!--        </el-col>-->

      <!--      </el-row>-->
      <!--         -->
      <div class="block">
        <el-table :data="tableData" border stripe>
          <!--           <el-table-column label="">-->
          <column v-for="(field,index) in fields" :key="index" :prop="field.prop" :label="field.label"></column>


        </el-table>
      </div>

      <!--         -->

    </el-card>
  </div>
</template>

<script>
  import Column from "./Column";
  import {webUtil} from '@/api/brand/brand';

  export default {
    name: "DataTableCard",
    data() {
      return {
        fields: [
          {
            label: '',
            prop: 'name'
          },
          {
            label: '',
            prop: 'letter'
          }
        ],
        tableData: [
          {
            date: 'ss',
            name: ''
          }

        ],
        totalCount: 0,
        query: {brand: '', letter: ''}
      }
    }

    ,
    methods: {
      async requestFirstPage(page, size) {
        await this.getTotalCount()
        // let {data:array} = await webUtil.showBrand(page,size)
        let name = ''
        let letter = ''
        this.$log.print(this.query)
        if (this.query != undefined) {
          name = this.query.brand == undefined ? '' : this.query.brand
          letter = this.query.letter == undefined ? '' : this.query.letter
          this.$log.print(name, letter)
        }
        let {data: array} = await webUtil.searchLike(page, size, name, letter)

        this.$log.print('$brand,,', array)
        this.tableData = array

        //
        this.$emit('requestPage', page, size)
      },
      async getTotalCount() {
        let {data: total} = await webUtil.getTotalCount()
        this.totalCount = total
        this.$emit('getTotalCount', total)
      },
      setQuery(query) {
        this.$log.print('s',query)
        // this.$set(this.query,'brand',query.brand)
        // this.$set(this.query,'letter',query.letter)
        this.query.brand = query.brand;
        this.query.letter = query.letter;
      }

    },
    components: {
      Column
    },
    mounted() {
      // this.getTotalCount()
      // this.requestFirstPage(1,5)
    }
  }
</script>

<style scoped>
  .el-card {
    margin-top: 12px
  }

  .block {
    margin-top: 10px;
  }


</style>



