<template>
  <el-container class="home-container">
    <!--     -->
    <el-header>
      <div>
<!--        <img src="" alt="">-->
        <div class="el-icon-iconName el-icon-setting"></div>
        <span></span>
      </div>
      <el-button type="info" @click="logout">
        
      </el-button>
    </el-header>
    <el-container>
      <!--     -->
      <el-aside :width="isCollapse? '64px': '200px'" comment="">
      <!--        -->
        <section class="toggle-button"  @click="changeCollapse">
          |||
        </section>
      <!--         -->
        <el-menu
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          :collapse="isCollapse"
          :collapse-transition="false"
          router
        >
          <!--          -->
          <el-submenu index="1">
          <!--              -->
            <template slot="title">
          <!--                -->
              <i class="el-icon-menu"></i>
          <!--                  -->
              <span></span>
            </template>
            <el-menu-item-group>
              <template slot="title"></template>
              <el-menu-item index="/shop"></el-menu-item>
              <el-menu-item index="/brand"></el-menu-item>
              <el-menu-item index="/category"></el-menu-item>
              <el-menu-item index="/sku"></el-menu-item>
<!--              <el-menu-item index="/category"></el-menu-item>-->
<!--              <el-menu-item index="/brand"></el-menu-item>-->
<!--              <el-menu-item index="/category"></el-menu-item>-->
            </el-menu-item-group>
<!--            <el-menu-item-group title="2">-->
<!--              <el-menu-item index="1-3">3</el-menu-item>-->
<!--            </el-menu-item-group>-->
<!--            <el-submenu index="1-4">-->
<!--              <template slot="title">4</template>-->
<!--              <el-menu-item index="1-4-1">1</el-menu-item>-->
<!--            </el-submenu>-->
          </el-submenu>

        </el-menu>

      </el-aside>
      <!--       -->
      <el-main>

        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  export default {
    name: "NavExample",
    data() {
      return {
        isCollapse:false
      }
    },
    mounted() {
    },
    methods: {
      logout() {
        window.localStorage.clear();
        let ret = Math.random()*100;
        this.$router.push('/?a=${ret}')
      },
      async getMenuItem() {
        let {data:res} = await this.$http.get('/menus')
        console.log(res)
      },
      changeCollapse() {
        this.isCollapse=!this.isCollapse
      }
    }
  }
</script>

<style  scoped lang="less">
  .el-header {
    background-color: #373d41;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #eeeeee;
    font-size: 20px;
  }

  .el-aside {
    background-color: #333744;


  }
  .toggle-button {
    text-align: center;
    font-size: 10px;
    line-height: 24px;
    color: white;
    letter-spacing: 0.3em;
    cursor: pointer;

  }

  .el-main {
    background-color: #EAEDF1;
  }
  .home-container {
    /* #app */
    height: 100%;
    /*  */

  }





</style>



